import React, { useState, useEffect } from 'react'
import MapGL, { Marker, Popup } from 'react-map-gl'

import * as coronaVirusData from './config/data.json'
import Constants from './config/constants'

const App = () => {
  const [selectedArea, setSelectedArea] = useState(null)
  const [viewPort, setViewPort] = useState({
    latitude: Constants.LATITUDE,
    longitude: Constants.LONGITUDE,
    width: Constants.WIDTH,
    height: Constants.HEIGHT,
    zoom: Constants.DEFAULT_ZOOM
  })

  useEffect(() => {
    const listener = (e) => {
      if (e.key === 'Escape') {
        setSelectedArea(null)
      }
    }
    window.addEventListener('keydown', listener)
    return () => {
      window.removeEventListener('keydown', listener)
    }
  }, [])

  return (
    <div>
      <MapGL
        {...viewPort}
        mapboxApiAccessToken={Constants.MAPBOX_TOKEN}
        mapStyle={Constants.MAPBOX_STYLE}
        onViewportChange={(viewPort) => setViewPort(viewPort)}
      >
        {coronaVirusData.features.map((area) => (
          <Marker
            key={area.properties.PARK_ID}
            latitude={area.geometry.coordinates[1]}
            longitude={area.geometry.coordinates[0]}
          >
            <button
              className='marker-btn'
              onClick={(e) => {
                e.preventDefault()
                setSelectedArea(area)
              }}
            >
              <img
                src='https://i.imgur.com/65Qkmce.png'
                alt='Corona Virus Icon'
                width='15px'
                height='15px'
              />
            </button>
          </Marker>
        ))}

        {selectedArea ? (
          <Popup
            latitude={selectedArea.geometry.coordinates[1]}
            longitude={selectedArea.geometry.coordinates[0]}
            onClose={() => {
              setSelectedArea(null)
            }}
          >
            <div>
              <h2>{selectedArea.properties.NAME}</h2>
              <p>{selectedArea.properties.DESCRIPTIO}</p>
            </div>
          </Popup>
        ) : null}
      </MapGL>
    </div>
  )
}

export default App
